.profile__photo-container {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-right: 24px;
    align-self: center;
    cursor: pointer;
    position: relative;
}


@media screen and (max-width: 500px) {
    .profile__photo-container {
        margin-right: 0;
    }  
}
